<template>
        <div class="row justify-content-center">
            <div class="col-lg-5 col-md-7">
                <div class="card bg-transparent shadow border-0">
                    <div class="card-header bg-transparent">
                        <div class="btn-wrapper text-center">
                            <h1><i class="ni ni-key-25"></i> Login</h1>
                        </div>
                    </div>
                    <div class="card-body px-lg-5 py-lg-5">
                        <form role="form">
                            <base-input class="form-control-plaintext"
                                        placeholder="Username"
                                        addon-left-icon="ni ni-circle-08"
                                        v-model="user.username">
                            </base-input>
                            <i class="text-danger" v-if="$v.user.username.$anyDirty && !$v.user.username.required">Field Required</i>

                            <base-input class="form-control-plaintext"
                                        placeholder="Password"
                                        type="password"
                                        addon-left-icon="ni ni-lock-circle-open"
                                        v-model="user.password">
                            </base-input>
                            <i class="text-danger" v-if="$v.user.password.$anyDirty && !$v.user.password.required">Field Required</i>

                            <div class="text-center">
                                <base-button block type="success" class="my-4" @click.native="userLogin">Log in</base-button>
                            </div>
                        </form>
                    </div>
                </div>
                <!-- <div class="row mt-3">
                    <div class="col-6">
                        <a href="#" class="text-light"><small>Forgot password?</small></a>
                    </div>
                    <div class="col-6 text-right">
                        <router-link to="/register" class="text-light"><small>Create new account</small></router-link>
                    </div>
                </div> -->
            </div>
        </div>
</template>
<script>
    import { api } from '@/main';
    import { required } from "vuelidate/lib/validators";

    export default {
        name: 'login',
        components: {
            // PaperTable,
        },
        data() {
          return {
            user: {
              username: '',
              password: ''
            }
          }
        },
        validations: {
            user: {
              username: { required },
              password: { required }
            },
        },
        async mounted(){
            this.checkToken();
          },
        methods: {
            checkToken(){
                if(localStorage.getItem('aistoken')){
                    this.$router.push("/dashboard");
                }
            },
            userLogin(){
                let self = this;
                self.$v.user.$touch();
                if(!self.$v.user.$error){
                    fetch(api + '/users/account/login/' + self.user.username + '/' + self.user.password).then(function(response) {
                        return response.json();
                    }).then(function(response) {
                        if(response.success) {
                            localStorage.setItem('aistoken', Math.floor((Math.random() * 10000000000) + 1));
                            localStorage.setItem('aisname', response.output);
                            self.$v.user.$reset();
                            self.$router.push("/dashboard");
                        }else if (!response.success) {
                            self.$v.user.$reset();
                            self.$swal({
                               title: 'Error',
                               text: response.errorMessage, //"Unable to create new brand",
                               icon: 'error' ,
                               confirmButtonColor: '#ef8157',
                               confirmButtonText: 'OK'
                            });
                        }
                    }).catch(function(error) {
                        console.log(error);
                        self.loadError = true;
                    });
                }
            },
        }
    }
</script>
<style>
</style>
